import React from "react";
import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  Row,
  Col,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  Form,
  FormGroup,
  Input,
  Label,
  Button,
} from "reactstrap";
import { Spinner } from "reactstrap";
import Logo from "../../assets/img/icons/logo11.png";
import "swiper/css/swiper.css";
import "../../assets/scss/plugins/extensions/swiper.scss";
import Plans from "./Plans";
import Man from "./ManipulatingSwiper";
import Checkbox from "../../components/@vuexy/checkbox/CheckboxesVuexy";
import { Check } from "react-feather";
import CreatableSelect from "react-select/creatable";
import classnames from "classnames";
// import RegisterFirebase from "./RegisterFirebase"
// import RegisterAuth0 from "./RegisterAuth0"
import registerImg from "../../assets/img/pages/register.jpg";
import "../../assets/scss/pages/authentication.scss";
import axios from "axios";
import "./reg.css";
axios.defaults.baseURL = "https://lastarzi2.herokuapp.com";
//axios.defaults.baseURL = "http://localhost:5000";

const prefixOptions = [
  { value: "Mr", label: "Mr", isSelected: true },
  { value: "Miss", label: "Miss", isFixed: true },
  { value: "Mrs", label: "Mrs", isFixed: true },
  { value: "Dr", label: "Dr", isFixed: false },
];

class Register extends React.Component {
  state = {
    activeTab: "1",
    next: false,
    accept: false,
    disbaleRegister: false,
    successRegister: false,
    errMsg: "",
    isLoading: false,
  };
  toggle = (tab) => {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  };
  callnext = (e) => {
    if (this.state.confirmPass === this.state.password) {
      this.setState({ next: !this.state.next });
    } else {
      this.setState({ errMsg: "Passwords do not match" });
      e.preventDefault();
    }
  };

  handleRegister = (e) => {
    this.setState({ disbaleRegister: true, isLoading: true });
    const {
      secretQustion1,
      secretQustion2,
      secretQustion3,
      secretAnswer1,
      secretAnswer2,
      secretAnswer3,
    } = this.state;
    e.preventDefault();
    const user = {
      email: this.state.email,
      name: this.state.name,
      username: this.state.email,
      mobile: this.state.mobile,
      password: this.state.password,
      status: "emailNotConfirmed",
      secrets: {
        secretQustion1,
        secretQustion2,
        secretQustion3,
        secretAnswer1,
        secretAnswer2,
        secretAnswer3,
      },
    };
    axios
      .post("/backendapi/saveuser", user)
      .then((res) => {
        this.setState({
          successRegister: true,
          isLoading: false,
        });
      })
      .catch((e) => {
        this.setState({
          errMsg: "Something went wrong. Please try after sometime!!",
        });
      });
  };
  render() {
    return (
      <div>
        <div>
          <br />
          <Row className="m-0 justify-content-center">
            <Col
              sm="8"
              xl="7"
              lg="10"
              md="8"
              className="d-flex justify-content-center"
            >
              <Card className="bg-authentication rounded-0 mb-0 w-100">
                <Row
                  className="m-0"
                  style={{
                    padding: "20px",
                  }}
                >
                  <Col lg="6">
                    <div
                      style={{
                        display: "flex",
                        width: "100%",
                        flexDirection: "row",
                        background: "white",
                        height: "80px",
                        // marginTop: "-110px",
                        // borderBottom: "1px solid grey",
                      }}
                    >
                      <img
                        style={{
                          height: "50px",
                          margin: "5px",
                          marginTop: "20px",
                          paddingBottom: "2px",
                        }}
                        alt="Logo"
                        src={Logo}
                        className="logo12"
                      />{" "}
                      <h2
                        style={{
                          color: "#2c2c2c",
                          fontWeight: "700",
                          marginTop: "28px",
                          textAlign: "center",
                          width: "70%",
                        }}
                      >
                        Last Arzi
                      </h2>
                    </div>
                    <Plans />
                    {/* <Man /> */}
                  </Col>
                  <Col lg="6" md="12" className="p-0">
                    {window.screen.width < 600 && (
                      <div
                        style={{
                          marginTop: "10px",
                          textAlign: "center",
                          display: "flex",
                          width: "100%",
                          flexDirection: "row",
                          background: "white",
                          //  height: "54px  ",
                          borderBottom: "1px solid grey",
                        }}
                      >
                        <img
                          style={{
                            height: "50px",
                            marginTop: "20px",
                            marginLeft: "20px",
                          }}
                          src={Logo}
                          className="logo12"
                        />{" "}
                        <h2
                          style={{
                            color: "#2c2c2c",
                            fontWeight: "700",
                            marginTop: "28px",
                            width: "70%",
                            // textAlign:"center"
                          }}
                        >
                          {" "}
                          Last Arzi
                        </h2>
                      </div>
                    )}
                    <Card className="rounded-0 mb-0 pt-1">
                      <CardHeader className="pb-1 pt-50">
                        <CardTitle
                          style={{
                            width: "100%",
                          }}
                        >
                          {/* <h4 className="mb-0">Create Account</h4> */}
                          <h2
                            className="mb-0"
                            style={{
                              color: "#2c2c2c",
                              fontWeight: "700",
                              marginTop: "7px",
                              textAlign: "center",
                            }}
                          >
                            Sign Up
                          </h2>
                        </CardTitle>
                      </CardHeader>
                      {/* {!this.state.successRegister && (
                        <p className="px-2 auth-title mb-0">
                          Fill the below form to create a new account.
                        </p>
                      )} */}
                      <p
                        className="px-2 auth-title mb-0"
                        style={{ color: "red" }}
                      >
                        {this.state.errMsg}
                      </p>
                      {this.state.successRegister && (
                        <p
                          className="px-2 auth-title mb-0"
                          style={{ color: "green" }}
                        >
                          {" "}
                          A confirmation email has been sent to your email.
                          Please confirm your email. If already confirmed{" "}
                          <div
                            onClick={() => {
                              this.props.history.push("/login");
                            }}
                          >
                            <u>Go to Login</u>
                          </div>
                        </p>
                      )}
                      {/* <Nav tabs className="px-2">
                    <NavItem>
                      <NavLink
                        className={classnames({
                          active: this.state.activeTab === "1"
                        })}
                        onClick={() => {
                          this.toggle("1")
                        }}
                      >
                        JWT
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink
                        className={classnames({
                          active: this.state.activeTab === "2"
                        })}
                        onClick={() => {
                          this.toggle("2")
                        }}
                      >
                        Firebase
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink
                        className={classnames({
                          active: this.state.activeTab === "3"
                        })}
                        onClick={() => {
                          this.toggle("3")
                        }}
                      >
                        Auth0
                      </NavLink>
                    </NavItem>
                  </Nav> */}
                      <CardBody className="pt-1">
                        {!this.state.next && (
                          <div>
                            <Form action="/" onSubmit={this.callnext}>
                              <FormGroup className="form-label-group">
                                <CreatableSelect
                                  isMulti={false}
                                  isClearable={false}
                                  options={prefixOptions}
                                  className="React"
                                  classNamePrefix="select"
                                  defaultValue={prefixOptions[0]}
                                  onChange={(e) =>
                                    this.setState({
                                      prefix: e && e.value ? e.value : null,
                                    })
                                  }
                                />
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="text"
                                  placeholder="Name"
                                  required
                                  value={this.state.name}
                                  onChange={(e) =>
                                    this.setState({ name: e.target.value })
                                  }
                                />
                                <Label>Full Name</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="email"
                                  placeholder="Email"
                                  required
                                  value={this.state.email}
                                  onChange={(e) =>
                                    this.setState({ email: e.target.value })
                                  }
                                />
                                <Label>Email</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="number"
                                  placeholder="Phone Number"
                                  required
                                  value={this.state.mobile}
                                  onChange={(e) =>
                                    this.setState({ mobile: e.target.value })
                                  }
                                />
                                <Label>Phone Number</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="password"
                                  placeholder="Password"
                                  required
                                  value={this.state.password}
                                  onChange={(e) =>
                                    this.setState({ password: e.target.value })
                                  }
                                />
                                <Label>Password</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="password"
                                  placeholder="Confirm Password"
                                  required
                                  value={this.state.confirmPass}
                                  onChange={(e) =>
                                    this.setState({
                                      confirmPass: e.target.value,
                                    })
                                  }
                                />
                                <Label>Confirm Password</Label>
                              </FormGroup>

                              <div
                                className="d-flex justify-content-between"
                                style={{ float: "right", marginRight: 0 }}
                              >
                                {/* <Button.Ripple
            color="primary"
            outline
            onClick={() => {
              this.props.history.push("/pages/login")
            }}
          >
            Login
          </Button.Ripple> */}
                                <Button.Ripple color="primary" type="submit">
                                  Next
                                </Button.Ripple>
                              </div>
                              <br />
                              <br />
                              <br />

                              <div
                                onClick={() => {
                                  this.props.history.push("/login");
                                }}
                                style={{ float: "right", color: "#4CAF50" }}
                              >
                                <span>Already registered?</span>
                                <u>Login</u>
                              </div>
                            </Form>
                          </div>
                        )}
                        {this.state.next && (
                          <div>
                            <Form action="/" onSubmit={this.handleRegister}>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="text"
                                  placeholder="Secret Question 1"
                                  required
                                  value={this.state.secretQustion1}
                                  onChange={(e) =>
                                    this.setState({
                                      secretQustion1: e.target.value,
                                    })
                                  }
                                />
                                <Label>Secret Question 1</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="password"
                                  placeholder="Secret Answer 1"
                                  required
                                  value={this.state.secretAnswer1}
                                  onChange={(e) =>
                                    this.setState({
                                      secretAnswer1: e.target.value,
                                    })
                                  }
                                />
                                <Label>Secret Answer 1</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="text"
                                  placeholder="Secret Question 2"
                                  required
                                  value={this.state.secretQustion2}
                                  onChange={(e) =>
                                    this.setState({
                                      secretQustion2: e.target.value,
                                    })
                                  }
                                />
                                <Label>Secret Question 2</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="password"
                                  placeholder="Secret Answer 2"
                                  required
                                  value={this.state.secretAnswer2}
                                  onChange={(e) =>
                                    this.setState({
                                      secretAnswer2: e.target.value,
                                    })
                                  }
                                />
                                <Label>Secret Answer 2</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="text"
                                  placeholder="Secret Question 3"
                                  required
                                  value={this.state.secretQustion3}
                                  onChange={(e) =>
                                    this.setState({
                                      secretQustion3: e.target.value,
                                    })
                                  }
                                />
                                <Label>Secret Question 3</Label>
                              </FormGroup>
                              <FormGroup className="form-label-group">
                                <Input
                                  type="password"
                                  placeholder="Secret Answer 3"
                                  required
                                  value={this.state.secretAnswer3}
                                  onChange={(e) =>
                                    this.setState({
                                      secretAnswer3: e.target.value,
                                    })
                                  }
                                />
                                <Label>Secret Answer 3</Label>
                              </FormGroup>
                              <FormGroup>
                                <Checkbox
                                  color="primary"
                                  icon={<Check className="vx-icon" size={16} />}
                                  label=" I accept the terms & conditions."
                                  //defaultChecked={false}
                                  value={this.state.accept}
                                  onChange={() => {
                                    this.setState({
                                      accept: !this.state.accept,
                                    });
                                  }}
                                />
                              </FormGroup>
                              <div className="d-flex justify-content-between">
                                <Button.Ripple
                                  color="primary"
                                  outline
                                  onClick={() => {
                                    this.setState({ next: false });
                                  }}
                                >
                                  Back
                                </Button.Ripple>

                                <Button.Ripple
                                  color={
                                    this.state.disbaleRegister ||
                                    !this.state.accept
                                      ? "primary"
                                      : "success"
                                  }
                                  type="submit"
                                  disabled={
                                    this.state.disbaleRegister ||
                                    !this.state.accept
                                  }
                                >
                                  {this.state.isLoading && (
                                    <Spinner color="danger" size="sm" />
                                  )}{" "}
                                  {this.state.isLoading
                                    ? "Registering..."
                                    : "Register"}
                                </Button.Ripple>
                              </div>
                              <br />
                              <br />
                              <br />
                              <div
                                onClick={() => {
                                  this.props.history.push("/login");
                                }}
                                style={{ float: "right", color: "#4CAF50" }}
                              >
                                <u>Go to Login</u>
                              </div>
                            </Form>
                          </div>
                        )}

                        {/* <TabContent activeTab={this.state.activeTab}>
                      <TabPane tabId="1">

                      </TabPane>
                      <TabPane tabId="2">
                        <RegisterFirebase />
                      </TabPane>
                      <TabPane tabId="3">
                        <RegisterAuth0 />
                      </TabPane>
                    </TabContent> */}
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </Card>
            </Col>

            {window.screen.width < 600 && (
              <Col lg="6" md="12" className="p-0">
                <Card className="bg-authentication rounded-0 mb-0 w-100">
                  <CardBody className="pt-1 pb-50">
                    <div style={{ width: "300px" }}>
                      <Plans />
                    </div>
                  </CardBody>
                </Card>
              </Col>
            )}
          </Row>
        </div>
      </div>
    );
  }
}
export default Register;
